import { GroupAddOutlined, MicNoneOutlined, MicOffOutlined, ModeCommentOutlined, MoodOutlined, PresentToAllOutlined, VideocamOffOutlined, VideocamOutlined } from "@mui/icons-material";
import { Button, IconButton, Stack } from "@mui/material";
import { useState } from "react";
import { useRTC } from "./context/rtcProvider";
import { useMedia } from "./context/media";

function Footer({ media, remote, video }) {
  const { peer } = useRTC();
  const { screenRecord, stop, startVideo } = useMedia();
  const [mic, setMic] = useState(false);
  const [vid, setVid] = useState(false);

  const handleMicToggle = () => {
    if (mic) {
      media({ audio: false });
    } else {
      media({ audio: true });
    }
    setMic(!mic);
  };

  const handleVidToggle = () => {
    if (vid) {
      media({ video: false });
      stop();
    } else {
      media({ video: true });
      startVideo(media, remote);
    }
    setVid(!vid);
  };

  const handleScreenShare = async () => {
    try {
      const constraints = {
        video: {
          cursor: "always", // Options: "always", "motion", "never"
          displaySurface: "monitor", // Options: "application", "browser", "monitor", "window"
        },
        audio: true,
      };

      const stream = await navigator.mediaDevices.getDisplayMedia(constraints);
      if (remote) {
        remote.srcObject = stream;
      }
    } catch (err) {
      console.error("Error accessing display media: ", err);
      alert(err);
    }
  };

  return (
    <div className="w-full bg-gray-800 p-4 bg-[black]">
      <Stack direction="row" justifyContent="space-around">
        <Stack direction="row" justifyContent="center" spacing={2}>
          <IconButton onClick={handleMicToggle} style={{ color: "white" }}>
            {mic ? <MicNoneOutlined /> : <MicOffOutlined />}
          </IconButton>
          <IconButton onClick={handleVidToggle} style={{ color: "white" }}>
            {vid ? <VideocamOutlined /> : <VideocamOffOutlined />}
          </IconButton>
        </Stack>
        <Stack direction="row" justifyContent="space-around">
          <IconButton style={{ color: "white" }}>
            <ModeCommentOutlined />
          </IconButton>
          <IconButton style={{ color: "white" }} onClick={handleScreenShare}>
            <PresentToAllOutlined />
          </IconButton>
          <IconButton style={{ color: "white" }}>
            <GroupAddOutlined />
          </IconButton>
          <IconButton style={{ color: "white" }}>
            <MoodOutlined />
          </IconButton>
        </Stack>
        <Stack>
          <Button variant="contained" style={{ backgroundColor: "red", color: "white" }}>
            Leave
          </Button>
        </Stack>
      </Stack>
    </div>
  );
}

export default Footer;
