import { useState, useRef } from "react";
import Footer from "./meetfooter";
import RTCProvider from "./context/rtcProvider";
import MediaProvider from "./context/media";
import "./App.css"

function VideoCall() {
  const [smallVideoPosition, setSmallVideoPosition] = useState({ top: "0", left:"0" });
  const [isDragging, setIsDragging] = useState(false);
  const [initialMousePos, setInitialMousePos] = useState({ x: 0, y: 0 });
  const remoteRef = document.getElementById("remote");
  const myVideoRef = useRef(null);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const trackPos = (data) => {
    setPosition({ x: data.x, y: data.y });
 };
  const handleMouseDown = (e) => {
    setIsDragging(true);
    setInitialMousePos({ x: e.clientX - smallVideoPosition.left, y: e.clientY - smallVideoPosition.top });
  };

  const handleMouseMove = (e) => {
    if (isDragging) {
      const newLeft = e.clientX - initialMousePos.x;
      const newTop = e.clientY - initialMousePos.y;
      //alert(newTop)
      setSmallVideoPosition({ top: newTop, left: newLeft });
    }
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  return (
    <div onMouseMove={handleMouseMove} onMouseUp={handleMouseUp}>
      <RTCProvider>
        <MediaProvider>
      
          <div
            style={{
              cursor: isDragging ? "grabbing" : "grab",
              position: "absolute",
              height: "100px",
              width: "200px",
              top: smallVideoPosition.top,
              left: smallVideoPosition.left,
              
              borderRadius: "10px",
              backgroundColor: "black",
              display: "inline-block",
            }}
            onMouseDown={handleMouseDown}
          >
            <video id="remotevideo" ref={remoteRef} style={{ borderRadius: "10px",position:"absolute" }} autoPlay muted />
          </div>
          <div>
            <video id="video">
                 </video>
           
          </div>
          <div style={{ display: "flex", justifyContent: "center", position: "absolute", bottom: "0px", backgroundColor: "black", width: "100%", height: "60px" }}>
            <Footer  video={myVideoRef} remote={remoteRef} />
          </div>
        </MediaProvider>
        
      </RTCProvider>

    </div>
  );
}

export default VideoCall;
