import { createContext, useContext, useEffect, useState } from "react";

const RTCContext = createContext();

function RTCProvider({ children }) {
    const [peer, setPeer] = useState(null);

    useEffect(() => {
        const peerConnection = new RTCPeerConnection({
            iceServers: [{ urls: 'stun:stun.l.google.com:19302' }]
        });
        setPeer(peerConnection);
    }, []);

    const myOffer = async () => {
        try {
            const offer = await peer.createOffer();
            await peer.setLocalDescription(offer);
            return offer;
        } catch (e) {
            console.error('Failed to create offer:', e);
        }
    };

    const myAnswer = async () => {
        try {
            const answer = await peer.createAnswer();
            await peer.setLocalDescription(answer);
            return answer;
        } catch (e) {
            console.error('Failed to create answer:', e);
        }
    };

    const myDataChannel = () => {
        return peer.createDataChannel("remotechannel");
    };

    return (
        <RTCContext.Provider value={{ myDataChannel, myAnswer, myOffer, peer }}>
            {children}
        </RTCContext.Provider>
    );
}

export function useRTC() {
    return useContext(RTCContext);
}

export default RTCProvider;
